body {
  letter-spacing: .04em;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.31;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

h1, h2, h3, h4, h5, h6, p, ul, ol {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

a, button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.wrapper {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

.mask {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(224, 227, 239, .7);
  justify-content: center;
  align-items: center;
  transition: all .6s;
  display: flex;
  position: fixed;
  top: 0;
}

.middle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bar {
  width: 10px;
  height: 70px;
  transform-origin: bottom;
  background: #fff;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  animation: loader 1.2s linear infinite;
  display: inline-block;
}

.bar1 {
  animation-delay: .1s;
}

.bar2 {
  animation-delay: .2s;
}

.bar3 {
  animation-delay: .3s;
}

.bar4 {
  animation-delay: .4s;
}

.bar5 {
  animation-delay: .5s;
}

.bar6 {
  animation-delay: .6s;
}

.bar7 {
  animation-delay: .7s;
}

.bar8 {
  animation-delay: .8s;
}

@keyframes loader {
  0% {
    background: tomato;
    transform: scaleY(.1);
  }

  50% {
    background: #9acd32;
    transform: scaleY(1);
  }

  100% {
    background: none;
    transform: scaleY(.1);
  }
}

.hide {
  opacity: 0;
}

